  transition: stroke 0.3s ease;
}
.feedback__form-field--checkbox input:checked + span {
  background-color: #ffffff;
  opacity: 1;
}
.feedback__form-field--checkbox input:checked + span svg {
  stroke: #316dc2;
}
.feedback__form-field--checkbox p {
  font-size: 12px;
}

.feedback__form-field.dropdown {
  margin: 0 0 20px;
}
@media (max-width: 1199px) {
  .feedback__form-field.dropdown {
    margin: 0 0 16px;
  }
}

.feedback__img {
  position: absolute;
  width: calc(100% - 200px);
  height: 100%;
  left: 50%;
  translate: -50% 0;
  border-radius: 16px;
  overflow: hidden;
}
@media (max-width: 1199px) {
  .feedback__img {
    width: calc(100% - 90px);
  }
}
@media (max-width: 767px) {
  .feedback__img {
    display: none;
  }
}
.feedback__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.dropdown {
  position: relative;
}
.dropdown--light .dropdown__name, .dropdown--light .dropdown__text {
  color: #484848;
}
.dropdown--light .dropdown__wrapper {
  border-color: rgba(72, 72, 72, 0.3);
}
.dropdown--light .dropdown__list {
  border-color: #484848;
}
.dropdown--light .dropdown__svg {
  stroke: #484848;
}

.dropdown__name {
  color: #f0efe9;
  opacity: 0.8;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  margin: 0 0 12px;
}
@media (max-width: 767px) {
  .dropdown__name {
    font-size: 12px;
    line-height: 1.2;
  }
}

.dropdown__wrapper {
  position: relative;
  width: 100%;
  background-color: transparent;
  border-radius: 8px;
  padding: 15px 36px 15px 20px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  height: 50px;
  cursor: pointer;
}
@media (max-width: 767px) {
  .dropdown__wrapper {
    padding: 11px 36px 10px 20px;
  }
}

.dropdown__text {
  font-size: 14px;
  color: #ffffff;
}

.dropdown__svg {
  position: absolute;
  top: 50%;
  translate: 0 -50%;
  right: 20px;
  stroke: #ffffff;
  fill: none;
  transition: rotate 0.3s ease;
}

.dropdown__list {
  width: 100%;
  max-height: 194px;
  padding: 0;
  margin: 0;
  flex-direction: column;
  border: 1px solid transparent;
  list-style: none;
  border-radius: 8px;
  overflow: hidden auto;
  position: absolute;
  top: 90px;
  z-index: 1;
  display: none;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.dropdown__item label {
  padding: 17px 20px;
  display: flex;
  background-color: #ffffff;
  margin: 0;
  cursor: pointer;
}
.dropdown__item span {
  color: inherit;
  font-size: 14px;
}

.dropdown__list.is-open {
  display: block;
  animation: fade 0.3s;
  opacity: 1;
}

.dropdown:has(.dropdown__list.is-open) .dropdown__svg {
  transform-origin: center;
  rotate: 180deg;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  transform: scale(1.1);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  transform: translateZ(0);
  background-color: rgba(0, 0, 0, 0.6);
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 23px 15px;
  }
}
.modal__overlay {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.modal__content {
  position: relative;
  border-radius: 16px;
  width: 510px;
  padding: 55px 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
}
.modal__content:has(.dropdown__list.is-open) {
  margin: 0 0 85px;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 37px 15px 40px;
  }
}
.modal__title {
  margin: 0 0 10px;
  font-size: 32px;
  font-weight: 400;
  line-height: 1;
  color: #316dc2;
}
@media (max-width: 1199px) {
  .modal__title {
    font-size: 28px;
    margin: 0 0 14px;
  }
}
@media (max-width: 767px) {
  .modal__title {
    font-size: 18px;
    margin: 0 0 8px;
  }
}
.modal__title br {
  display: none;
}
@media (max-width: 767px) {
  .modal__title br {
    display: block;
  }
}
.modal__desc {
  line-height: 1.6;
  margin: 0 0 20px;
  text-align: center;
}
@media (max-width: 1199px) {
  .modal__desc {
    line-height: 1.25;
  }
}
@media (max-width: 767px) {
  .modal__desc {
    font-size: 14px;
    line-height: 1.4;
    margin: 0 0 19px;
    max-width: 85%;
  }
}
.modal__close-btn {
  position: absolute;
  top: 30px;
  right: 30px;
  width: 22px;
  aspect-ratio: 1;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .modal__close-btn {
    width: 16px;
    top: 20px;
    right: 20px;
  }
}
.modal__close-btn::before, .modal__close-btn::after {
  content: "";
  position: absolute;
  width: 22px;
  height: 1px;
  background-color: #316dc2;
  top: 50%;
  left: 50%;
  translate: -50% 0;
  transform-origin: center;
}
@media (max-width: 767px) {
  .modal__close-btn::before, .modal__close-btn::after {
    width: 16px;
  }
}
.modal__close-btn::before {
  rotate: 45deg;
}
.modal__close-btn::after {
  rotate: -45deg;
}
.modal__close-btn svg {
  display: block;
}
.modal__form-field {
  margin: 0 0 15px;
  width: 100%;
}
@media (max-width: 1199px) {
  .modal__form-field {
    margin: 0 0 14px;
  }
}
.modal__form-field:last-of-type {
  margin: 0 0 40px;
}
@media (max-width: 1199px) {
  .modal__form-field:last-of-type {
    margin: 0 0 20px;
  }
}
.modal__form-field label {
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.modal__form-field--text input, .modal__form-field--text textarea {
  width: 100%;
  background-color: transparent;
  border: 1px solid rgba(72, 72, 72, 0.3);
  border-radius: 8px;
  padding: 16px 25px;
  height: 44px;
}
.modal__form-field--text span, .modal__form-field--text p {
  opacity: 0.8;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  margin: 0;
}
.modal__form-field--textarea textarea {
  height: 98px;
}
.modal__form-field--checkbox span, .modal__form-field--checkbox p {
  opacity: 0.8;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  margin: 0;
}
.modal__form-field--checkbox label {
  flex-direction: row;
  align-items: center;
  gap: 0;
  padding: 0 0 0 30px;
  position: relative;
  cursor: pointer;
  min-height: 20px;
}
.modal__form-field--checkbox span {
  display: block;
  width: 20px;
  aspect-ratio: 1;
  border-radius: 2px;
  position: absolute;
  left: 0;
  top: 50%;
  translate: 0 -50%;
  background-color: transparent;
  border: 1px solid #316dc2;
  opacity: 0.7;
  transition: background-color 0.3s ease, opacity 0.3s ease;
}
.modal__form-field--checkbox svg {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  fill: transparent;
  transition: stroke 0.3s ease;
}
.modal__form-field--checkbox input:checked + span {
  background-color: #316dc2;
  opacity: 1;
}
.modal__form-field--checkbox input:checked + span svg {
  stroke: #ffffff;
}
.modal__form-field--checkbox p {
  font-size: 12px;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: auto;
}
.modal iframe {
  max-width: 100%;
  border: none;
}

@media (max-width: 767px) {
  .modal .btn {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .modal .dropdown__wrapper {
    height: 44px;
  }
}

@media (max-width: 767px) {
  .modal .dropdown__name {
    font-size: 14px;
    margin: 0 0 6px;
  }
}

